<script>

    export default {
        name: 'hostVideo',
        created() {
            if(this.$isMobile()) {
                this.$router.push('/liveStream/hostVideoMobile/'+this.$route.params.id)
            } else {
                this.$router.push('/liveStream/hostVideoPc/'+this.$route.params.id)
            }
        }
    }
</script>
